<template lang="pug">
  .ui--babysitter-custom-fields
    .row.mt-5

      //- KIND

      .col-lg-12
        h2.h2 What kind of babysitting do you offer?
        .card.shadow.px-4.py-4
          tag-check-box-group.is-flex.space-between(
            kind="kind"
            v-model="form.kind"
          )

    .row.mt-5

      //- EXPERIENCE

      .col-lg-6
        h2.h2 What experience do you have?
        .card.shadow.px-4.py-4
          tag-check-box-group(
            kind="experience"
            v-model="form.experience"
          )

      //- AGE RANGES

      .col-lg-6
        h2.h2 Age ranges you already babysitted
        .card.shadow.px-4.py-4
          tag-check-box-group.is-flex(
            kind="age_ranges"
            v-model="form.age_ranges"
          )

    .row.mt-5

      //- QUALIFICATIONS

      .col-lg-6
        h2.h2 Any qualification?
        .card.shadow.px-4.py-4
          tag-check-box-group(
            kind="qualification"
            v-model="form.qualification"
          )

      //- LANGUAGES

      .col-lg-6
        h2.h2 Languages
        .card.shadow.px-4.py-4
          .is-flex
            language-selector(
              @select="addLanguage"
              :show-choose="true"
            )

            b-button.mr-2.is-flex.valign(@click="removeLanguage(lang)" v-for="lang in form.languages" variant="white")
              i.flag-icon.mr-2(:class="'flag-icon-'+lang.substring(3).toLowerCase()")
              i.mdi.text-grey remove_circle_outline

</template>

<script>
  import {mapGetters, mapActions} from "vuex";
  import LanguageSelector from "../ui/language-selector";
  import TagCheckBoxGroup from "../ui/tagCheckBoxGroup";

  export default {
    name: "BabysitterCustomFields",
    components: {TagCheckBoxGroup, LanguageSelector},
    props: [],
    data() {
      return {
        // UX
        isLoading: false,
        form: {
          kind: [],
          experience: [],
          qualification: [],
          age_ranges: [],
          languages: []
        },
      };
    },
    computed: {
      ...mapGetters(['rates']),
    },
    watch: {
      form: {
        deep: true,
        handler(newVal) {
          this.setProductCustomFields(newVal)
        }
      }
    },
    mounted() {
    },
    methods: {
      ...mapActions(['setProductCustomFields']),
      addLanguage(language) {
        this.form.languages.push(language)
      },

      removeLanguage(language) {
        this.form.languages = this.form.languages.filter(lang => lang != language)
      }

    },
  };
</script>


