    .dropdown-menu {
      min-width: 4rem;
    }

    .custom-control-label {
      transition: color .15s ease-in-out;
      padding:    .25rem 0;
      cursor:     pointer;
      flex-grow:  1;

      &:hover {
        color: $primary;
      }
    }

    .custom-control-label::after,
    .custom-control-label::before {
      top: 0.45625rem;
    }

    #age_ranges {
      flex-direction: column;
    }

    @media (min-width: 992px) {
      #age_ranges {
        min-height: 203px;
      }
    }

  }
</style>
