<template lang="pug">
  .new-product
    h1.h1.my-4 Fill your ad
    .row
      .col-lg-6
        h2.h2.my-2.mb-3 Give us your best smile
        gallery-dropzone.card.shadow.px-4.py-4
        //video-dropzone.card.shadow( :class="{'px-4 py-4': !attachments[0]}" )

      .col-lg-6.product--details
        h2.h2.my-2.mb-3 Details
        .card.shadow.px-4.py-4

          .product__title.is-flex.space-between
            b-form-group.w-100.mr-2(label="Title")
              b-form-input#title(
                :state="$v.form.title.$dirty ? !$v.form.title.$error : null"
                expanded
                v-model.trim="form.title" required="" autofocus="" size="is-medium" placeholder="Title")
              b-form-invalid-feedback
                | Please fill this field

            b-form-group(label="Hourly rate")
              b-input-group(append="€")
                b-input#amount.pr-0(
                v-model.trim="form.rates_attributes[0].amount" type="number")

          .product__description
            b-form-group
              .row.valign.mb-1
                .col
                  span
                    | Description
                .col.ml-auto.text-right
                  editor-menu-bar.ml-auto(:editor="editor")
                    div(slot-scope="{ commands, isActive }")
                      .buttons.has-addons
                        b-button.mr-1(
                          size="sm"
                          :variant="isActive.heading({ level: 2 }) ? 'secondary' : 'outline-secondary'"
                          @click="commands.heading({ level: 2 })")
                          | H2
                        b-button.mr-1(
                          size="sm"
                          :variant="isActive.heading({ level: 3 }) ? 'secondary' : 'outline-secondary'"

                          :class="{ 'is-active': isActive.heading({ level: 3 }) }"
                          @click="commands.heading({ level: 3 })")
                          | H3
                        b-button(
                          size="sm"
                          :variant="isActive.bold() ? 'secondary' : 'outline-secondary'"
                          @click="commands.bold")
                          | B

              .content
                editor-content(
                  :editor="editor"
                )

          .product__description

    babysitter-custom-fields

    .text-right
      b-button(
        :variant="'outline-secondary'"
        @click="save")
        | SAVE

</template>

<script>
  import {validationMixin} from 'vuelidate'
  import {required, minLength, email, sameAs} from 'vuelidate/lib/validators'

  import {mapGetters, mapActions} from "vuex";

  import {Editor, EditorContent, EditorMenuBar} from 'tiptap'
  import {
    Blockquote,
    CodeBlock,
    HardBreak,
    Heading,
    OrderedList,
    BulletList,
    ListItem,
    TodoItem,
    TodoList,
    Bold,
    Code,
    Italic,
    Link,
    History,
  } from 'tiptap-extensions'
  import GalleryDropzone from "../ui/gallery-dropzone";
  import VideoDropzone from "../ui/video-dropzone";
  import PackagesList from "../ui/packages-list";
  import BabysitterCustomFields from "../sections/babysitter-custom-fields";

  export default {
    name: "NewProduct",
    components: {
      BabysitterCustomFields,
      PackagesList,
      VideoDropzone,
      GalleryDropzone,
      // EDITOR
      EditorContent,
      EditorMenuBar,
    },
    mixins: [validationMixin],
    validations: {
      form: {
        title: {
          required,
        },
      }
    },
    props: [],
    data() {
      return {
        // FORM
        form: {
          title: '',
          description: '',
          cover_picture: null,
          is_listed: false,
          rates_attributes: [
            {
              amount: null,
              currency: 'EUR',
              duration_minutes: 60,
              quantity: 1,
            }
          ]
        },
        editor: null,
        // UX
        isLoading: false,
      };
    },
    computed: {
      ...mapGetters(['attachments', 'user', 'product', 'attachments']),

    },
    mounted() {
      this.resetAttachment()
      // EDITOR
      this.editor = new Editor({
        extensions: [
          new Blockquote(),
          new BulletList(),
          new CodeBlock(),
          new HardBreak(),
          new Heading({levels: [2, 3, 4]}),
          new ListItem(),
          new OrderedList(),
          new TodoItem(),
          new TodoList(),
          new Bold(),
          new Code(),
          new Italic(),
          new Link(),
          new History(),
        ],
        content: ''
      })
      this.form.title = `${this.user.first_name || ''}${this.user.last_name ? this.user.last_name[0]+' .' : ''}`
    },
    beforeDestroy() {
      this.editor.destroy()
    },
    methods: {
      ...mapActions(['saveProduct', 'resetAttachment']),
      onSubmit(evt) {
        this.$v.form.$touch()
        if (this.$v.form.$anyError) {
          console.log(this.$v.form)
          return
        }
        this.isLoading = true;
        evt.preventDefault();
      },

      save() {
        // EDITOR
        this.form.description = this.editor.view.dom.innerHTML;
        this.form.tags = this.product.tags
        this.form.attachments = this.attachments

        this.saveProduct(this.form).then(() => {
          this.$toast.success('Ad successfully created')
        })

      },

    },
  };
</script>


