    h1:not(.h1), h2:not(.h2), h3:not(.h3), h4:not(.h4), h5:not(.h5), h6:not(.h6) {
      color:       #363636;
      font-weight: 600;
      line-height: 1.125;
      font-family: Montserrat;
    }

    .ProseMirror h2 {

