<template lang="pug">
  .ui--video-dropzone
    .main-video

      .dropzone.text-grey(
        v-if="!attachments[0]"
        :class="{'dropzone--hover': dragging, isLoading: uploading}"
        @dragenter.stop.prevent="dragging = true"
        @dragover.stop.prevent=""
        @dragleave.stop.prevent="dragging = false"
        @drop.stop.prevent="onDrop($event, 'video1')"
      )
        | DRAG & DROP A VIDEO HERE...
      video.rounded(
        controls
        v-if="attachments[0]"
        :src="attachments[0].video.url"
      )

</template>

<script>
  import {mapGetters, mapActions} from "vuex";

  export default {
    name: "VideoDropzone",
    components: {
    },
    
    props: [],
    data() {
      return {
        // UX
        dragging: false,
        uploading: false,
        isLoading: false
      };
    },
    computed: {
      ...mapGetters(['attachments']),
    },
    mounted() {
    },
    methods: {
      ...mapActions(['saveAttachment']),
      
      onDrop(ev, videoId) {
        const {dataTransfer} = ev;
        this.uploading = true;
        this.saveAttachment({
          file: dataTransfer.files[0]
        }).then(res => {
          this.uploading = false;
          this.resetUploadingUi()
        }, err => {
          this.$toast.error('The file sent couldn\'t be processed. Please make sure you send a video.')
          this.resetUploadingUi()
        });
      },
      
      resetUploadingUi() {
        this.dragging = false
        this.uploading = false
      }
    },
  };
</script>


<style lang="scss">
  .ui--video-dropzone {
    background-size: contain;
    background-position: center center;
    background-repeat: no-repeat;
    .main-video {
      text-align: center;
      min-height: 300px;
      .dropzone {
        min-height: 300px;
      }
      img {
        height: 270px;
        width:  auto
      }
      video {
        width: 100%;
      }
    }
  }
</style>
