<template lang="pug">
  .ui--gallery-dropzone(
          :style="attachments[0] ? backgroundStyle : ''"
        )
    .main-picture

      .dropzone.text-grey(
        v-if="!attachments[0]"
        :class="{'dropzone--hover': dragging.picture1, isLoading: uploading.picture1}"
        @dragenter.stop.prevent="dragging.picture1 = true"
        @dragover.stop.prevent=""
        @dragleave.stop.prevent="dragging.picture1 = false"
        @drop.stop.prevent="onDrop($event, 'picture1')"
      )
        | DRAG & DROP A PICTURE HERE...


    .secondary-pictures.is-flex.space-between.mt-3
      div(v-for="nb in [2,3,4,5,6]")
        .dropzone.dropzone-mini(
          v-if="!attachments[nb-1]"
          :class="{'dropzone--hover': dragging['picture'+nb], isLoading: uploading['picture'+nb]}"
          @dragenter.stop.prevent="dragging['picture'+nb] = true"
          @dragover.stop.prevent=""
          @dragleave.stop.prevent="dragging['picture'+nb] = false"
          @drop.stop.prevent="onDrop($event, 'picture'+nb)"
        )
        .is-flex.valign.border.rounded.p-2(
          v-if="attachments[nb-1]"
          :style="`background-image:url('${attachments[nb-1].picture.url}')`"
        )
</template>

<script>
  import {mapGetters, mapActions} from "vuex";

  export default {
    name: "GalleryDropzone",
    components: {
    },
    
    props: [],
    data() {
      return {
        // UX
        dragging: {
          picture1: false,
          picture2: false,
          picture3: false,
          picture4: false,
          picture5: false,
          picture6: false,
          picture7: false,
        },
        uploading: {
          picture1: false,
          picture2: false,
          picture3: false,
          picture4: false,
          picture5: false,
          picture6: false,
          picture7: false,
        },
        isLoading: false,
      };
    },
    computed: {
      ...mapGetters(['attachments']),
      backgroundStyle() {
        if (this.attachments[0]) {
          let style = `background-image:url('${this.attachments[0].picture.url}');`
          if (this.attachments[0].picture.height / this.attachments[0].picture.width <= 0.75) {
            style += `background-size: cover;`
          }
          return style;
        } else {
          return null;
        }
      }
    },
    mounted() {
    },
    methods: {
      ...mapActions(['saveAttachment']),
      
      onDrop(ev, pictureId) {
        const {dataTransfer} = ev;
        this.uploading[pictureId] = true;
        this.saveAttachment({
          file: dataTransfer.files[0]
        }).then(res => {
          this.uploading[pictureId] = false;
          this.resetUploadingUi()
        }, err => {
          this.$toast.error('The file sent couldn\'t be processed. Please make sure you send an image.')
          this.resetUploadingUi()
        });
      },
      
      resetUploadingUi() {
        this.dragging = {
          picture1: false,
          picture2: false,
          picture3: false,
          picture4: false,
          picture5: false,
          picture6: false,
          picture7: false,
        }
        this.uploading = {
          picture1: false,
          picture2: false,
          picture3: false,
          picture4: false,
          picture5: false,
          picture6: false,
          picture7: false,
        }
      }
    },
  };
</script>


<style lang="scss">
  .ui--gallery-dropzone {
    background-size: contain;
    background-position: center center;
    background-repeat: no-repeat;
    .main-picture {
      text-align: center;
      min-height: 270px;
      img {
        height: 270px;
        width:  auto
      }
    }

    .secondary-pictures {
      text-align: center;

      img {
        width:  99%;
        height: auto
      }

      .is-flex {
        min-height:      90px;
        width:           90px;
        background-size: cover;
        background-color: rgba(255, 255, 255, 0.8);
      }
    }

  }
</style>
